.addnew {
  flex: 0.4;
  border-right: 1px solid var(--twitter-background);
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  z-index: 1;
}
.widgets {
  flex: 0.3;
}
.widgets__input {
  display: flex;
  align-items: center;
  background-color: var(--twitter-background);
  padding: 10px;
  border-radius: 20px;
  margin-top: 10px;
  margin-left: 20px;
}
.widgets__input > input {
  border: none;
  background-color: var(--twitter-background);
}
.widgets__searchIcon {
  color: grey;
}
.widgets__widgetContainer {
  margin-top: 15px;
  margin-left: 20px;
  padding: 20px;
  background-color: #f5f8fa;
  border-radius: 20px;
}

.widgets__widgetContainer > h2 {
  font-size: 18px;
  font-weight: 800;
}

.widget__optie {
  display: flex;
  padding: 20px;

  cursor: pointer;
  border-bottom: 2px solid #f6f6f6;
}

.widget__optie:hover {
  background-color: #ebebeb;
}
.widget__optie__info > h2 {
  font-size: 16px;
  margin-bottom: 8px;
}

.widget__optie__info {
  margin-left: 15px;
}

a {
  text-decoration: none !important;
  color: black;
}
